import { __ } from '@wordpress/i18n';
import { InspectorControls, AlignmentToolbar, BlockControls } from '@wordpress/block-editor';
import { PanelBody, __experimentalUnitControl as UnitControl, SelectControl, PanelRow, RangeControl, ToggleControl } from '@wordpress/components';

import { Label, InlineDetailMediaUpload } from '../../../../../Components';
import { pxUnit, perUnit } from '../../../../../Components/utils/options';

import { panoTypes } from '../../../utils/options';

const Settings = ({ attributes, setAttributes }) => {
	const { panoType, panoImage, panoVideo, width, height, autoRotate, rotateSpeed, hideControl, autoPlay, videoMute, videoLoop, alignment } = attributes;

	return <>
		<InspectorControls>
			<div className='bpgbInspectorInfo'>
				Need more block like this? Checkout the bundle  <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<PanelBody className='bPlPanelBody' title={__('Panorama Settings', 'panorama-block')}>
				<PanelRow>
					<Label className=''>{__('Panorama Type:', 'panorama-block')}</Label>

					<SelectControl value={panoType} onChange={val => setAttributes({ panoType: val })} options={panoTypes} />
				</PanelRow>

				<Label>{__('Panorama Source:', 'panorama-block')}</Label>
				{'image' === panoType && <InlineDetailMediaUpload value={panoImage} types={['image']} onChange={val => setAttributes({ panoImage: val })} placeholder={__('Enter Image URL', 'panorama-block')} />}

				{'video' === panoType && <InlineDetailMediaUpload value={panoVideo} types={['video']} onChange={val => setAttributes({ panoVideo: val })} placeholder={__('Enter Video URL', 'panorama-block')} />}

				<UnitControl className='mt20' label={__('Width:', 'panorama-block')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit(), perUnit()]} />

				<UnitControl className='mt20' label={__('height:', 'panorama-block')} labelPosition='left' value={height} onChange={val => setAttributes({ height: val })} units={[pxUnit(), perUnit()]} />

				{'video' === panoType && <>
					<ToggleControl className='mt20' label='Auto Play' checked={autoPlay} onChange={val => setAttributes({ autoPlay: val })} />

					<ToggleControl className='mt10' label='Video Mute' checked={videoMute} onChange={val => setAttributes({ videoMute: val })} />

					<ToggleControl className='mt10' label='Video Loop' checked={videoLoop} onChange={val => setAttributes({ videoLoop: val })} />
				</>}


				{'image' === panoType && <>
					<ToggleControl className='mt20' label='Auto Rotate' checked={autoRotate} onChange={val => setAttributes({ autoRotate: val })} />

					{autoRotate && <>
						<Label>{__('Auto Rotate Speed:', 'panorama-block')}</Label>
						<RangeControl value={rotateSpeed} onChange={val => setAttributes({ rotateSpeed: val })} min={-100} max={100} step={1} />
					</>}

					<ToggleControl className='mt20' label='Hide Control' checked={hideControl} onChange={val => setAttributes({ hideControl: val })} />
				</>}
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Panorama Alignment')} alignmentControls={[
				{ title: __('Panorama in left', 'panorama-block'), align: 'left', icon: 'align-left' },
				{ title: __('Panorama in center', 'panorama-block'), align: 'center', icon: 'align-center' },
				{ title: __('Panorama in right', 'panorama-block'), align: 'right', icon: 'align-right' }
			]}
			/>
		</BlockControls>
	</>
};
export default Settings;