import { useEffect } from 'react';

import Settings from './Settings/Settings';
import { config } from '../../utils/functions';

const Edit = (props) => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { panoType, panoImage, panoVideo, width, height, autoRotate, rotateSpeed, hideControl, autoPlay, videoMute, videoLoop, alignment } = attributes;

	useEffect(() => {
		clientId && setAttributes({ cId: clientId });
	}, [clientId]); // Set & Update clientId to cId

	useEffect(() => {
		const containerEl = document.querySelector(`#bpgbPanorama-${clientId} .bpgbPanorama`);
		containerEl.innerHTML = '';

		config(attributes, containerEl);
	}, [panoType, panoImage, panoVideo, width, height, autoRotate, rotateSpeed, hideControl, autoPlay, videoMute, videoLoop]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} />

		<div className={className} id={`bpgbPanorama-${clientId}`}>
			{!isSelected && <div className='panoramaEditorAbsolute'></div>}

			<style dangerouslySetInnerHTML={{
				__html: `
				#bpgbPanorama-${clientId}{
					text-align: ${alignment};
				}
				#bpgbPanorama-${clientId} .bpgbPanorama{
					width: ${width};
					height: ${height};
				}`
			}} />

			<div className='bpgbPanorama'></div>
		</div>
	</>
};
export default Edit;